.shipping-container {
    border-radius: 5px;
    max-width: 600px;
    margin: auto;
    margin-top: 0px;
    overflow: scroll;
}
.shipping-container input {
    background: rgb(240, 240, 240);
    border: none;
    height: 40px;
    width: 98%;
    margin-left: 1%;
}
.shipping-container input:focus {
    background: rgb(240, 240, 240);
    outline: 0 none;
    box-shadow: 0 1px 1px rgba(229, 103, 23, 0.075) inset,
        0 0 8px rgb(197, 197, 197);
    outline: 0 none;
}
.shipping-container select {
    background: rgb(240, 240, 240);
    border: none;
    height: 40px;
    width: 98%;
    margin-left: 1%;
}
.shipping-container select:focus {
    background: rgb(240, 240, 240);
    outline: 0 none;
    box-shadow: 0 1px 1px rgba(229, 103, 23, 0.075) inset,
        0 0 8px rgb(197, 197, 197);
    outline: 0 none;
}
.shipping-container label {
    font-weight: bold;
}


@media (orientation: portrait) {
    .shipping-container {
        border-radius: 5px;
        max-width: 600px;
        margin: auto;
        margin-top: 0px;
        overflow: hidden;
    }
  
}
